<template>
    <div v-if="ready" style='position:relative;'>
        <form id='form' enctype="multipart/form-data" @submit.prevent="handleSubmit">
            <div class="card">
                <div class="card-header">
                    <strong>Lesson Swap</strong>
                </div>    
                <div class="card-body out_wrapper">
                    <div class="col-12 flex" style='margin-bottom:0.75rem;padding-right:0;padding-left:0'>
                        <div class="col-3" style='padding-left:0'>
                            <input type="text" placeholder="Search User" onfocus="this.value=''" v-model="key" class="form-control" @keyup="search_user"/>
